html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  --active-link-color: #00acdc;
  --link-color: #000;
  margin: 0;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav__link {
  color: var(--link-color);
  text-transform: uppercase;
  outline: none;
  margin: 0;
  padding: 0;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.header__logo {
  display: inline-block;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__item {
  float: left;
  position: relative;
}

.nav__list .nav__item:not(:last-child) {
  margin-right: 20px;
}

.is-active {
  color: var(--active-link-color);
}

.is-active:after {
  content: "";
  background-color: var(--active-link-color);
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav__link:hover {
  color: var(--active-link-color);
}
